body {
  background-color: #FFCC00;
  padding: 20px;
  margin: 0;
}
h1, h2, p, ul, li {
  font-family: sans-serif;
}
ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0;
}
ul.header {
  background-color: #111;
  padding: 0;
}
ul.header li a {
  color: #FFF;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block;
}
.content {
  background-color: #FFF;
  padding: 20px;
}
.content h2 {
  padding: 0;
  margin: 0;
}
.content li {
  margin-bottom: 10px;
}
.homepage-cover{
  background-size: auto
}
.firstName, .lastName{
  display: inline-block;
}
.contact {
  border:2px dotted;
  margin:5px;
  padding-left:10px;
  background-color:antiquewhite;
}
.contact-list-title{
  text-align: center;
  font-weight: bold;
  color:darkblue;
  font-size: 20px;
}
.main-content{
  background-color: lightgrey;

}
.input-search{
  margin: 0 auto; 
  width:100%;
  text-align: right;
}
.input-search-field{
  margin: 0 auto; 
  margin-right: 1%;
  margin-left:1%;
  -webkit-box-sizing: border-box;
  /*box-sizing: border-box;*/
  text-align: right;
  width: 98% ;
  }