
  .content-post {
    border: 1px solid #35424c;
    margin-top:20px;
    width: 50%;
    padding: 5px;
    display: inline-block;
    width:100%;
    background-color: #35424c;
  }
  .App-header{
     border: 1px solid #35424c;
      margin-top:20px;
      width: 50%;
      padding: 5px;
      display: inline-block;
    }
    .profile-picture{
      object-fit: cover;
  width: 80%;
  border-radius: 50%;
  
    
  }
   .profile-picture-div{
  width:15%;
    display: inline-block;
    float: left;
    overflow: hidden;
    width: 15%;
  }
  .data{
    display:block;
    border-radius: 50%;
    color:white;
    float: right;
    width: 85%;
  
  }
  .data, .profile-picture-div{
    height: 100%;
    display: inline-block;
    
  
  }
  .name, .date, .nickname{
    display: inline-block;
    float: left;
  }
  .nickname, .date{
    color: #7d8d99;
  }
  p{
    margin: 0;
    font-size: 1em;
  }
  .image{
  width:100%;
  border-radius: 15px
  }
  .opinion{
    float: left;
  }
  .image{
    margin-top: 5px;
    margin-bottom: 5px;;
  }
  .expand-icon{
    float:right;
    margin-right: 10px;
  }
  .footer{
    color:#64727e;
    display: inline-block;
    margin-left:10%;
    font-size: 1em;
  }
   .post-blocks{
    display: flex;
    
